export default {
  props: {
    item: {
      type: Object
    },
    show: {
      type: Boolean,
      default: true
    },
	latitude:{
		type:Number
	},
	longitude:{
		type:Number
	}
  },
  methods: {
    goMap() {
      let that = this;
      uni.openLocation({
        latitude: parseFloat(that.item.latitude),
        longitude: parseFloat(that.item.longitude),
        name: that.item.shopName,
        address: that.item.address,
        success: function() {
          console.log("success");
        },
        fail(e) {
          console.log(e);
        }
      });
    },
	goShopDetails(e) {
		let stringId = e || "";
		uni.navigateTo({
		  url: `shopDetail?stringId=${stringId}&longitude=${this.longitude}&latitude=${this.latitude}` 
		});
	}
  }
};
